import React, {Component} from "react";
import {connect} from "react-redux";
import OnlineUtils from "eshop/utils/OnlineUtils";
import {getChangedDevice, getPriceIsNet} from "eshop/modules/cart/selectors"
import ProductOfferInstalmentPriceTooltipComponent
    from "eshop/modules/simfree/components/ProductOfferInstalmentPriceTooltipComponent"


const DeviceInstalmentPeriod = (props) => {
    let from = props.monthFrom || 0;
    let to = props.monthTo || props.maxInstalment;
    let instalmentPeriodLength = to - from + 1.0;
    let test = "";
    let price=OnlineUtils.getPriceFromPaymentsObject(props,props.showNet);
    return (
        <span
            className="u-block">{OnlineUtils.formatPrice(price)} zł x {instalmentPeriodLength} {OnlineUtils.odmienRaty(instalmentPeriodLength)}</span>
    )

}

class DevicePayments extends React.Component {

    render() {
        let useCartPrices = this.props.deviceToChange && !!this.props.product.variantList.find(variant=> this.props.deviceToChange.productCode == variant.productId);


        if (this.props.product.inOffer != null) {
            let instalmentPayments = this.props.product.inOffer.price.base.devicePayments;
            let oneTimePayment = OnlineUtils.formatPrice(this.props.showNet ? this.props.product.inOffer.price.base.oneTimePaymentNet : this.props.product.inOffer.price.base.oneTimePaymentGross)

            if (useCartPrices) {
                instalmentPayments = this.props.deviceToChange.monthlyPrices;
                oneTimePayment = OnlineUtils.formatPrice(this.props.showNet ? this.props.deviceToChange.checkoutPrice.net : this.props.deviceToChange.checkoutPrice.gross)

            }
            console.log("DEVICE FROM CART TO CHANGE", this.props.deviceToChange);
            return (<DeviceInstalmentPayments
                    product={this.props.product}
                    marketIsB2B={this.props.marketIsB2B}
                    showNet={this.props.showNet}
                    proposition={this.props.proposition}
                    instalmentTooltipDescription={this.props.instalmentTooltipDescription}
                    instalmentPayments={instalmentPayments}
                    oneTimePayment={oneTimePayment}

                />

            )
        }
        else return (
            <DeviceSimfreePayments product={this.props.product}
                                   productVariant={this.props.productVariant}
                                   instalmentUrl={this.props.instalmentUrl}
                                   showNet={this.props.showNet}
                                   marketIsB2B={this.props.marketIsB2B}/>
        )
    }
}

class DeviceSimfreePayments extends React.Component {
    render() {

        return (<div>
            <p className="g-brand1-c u-font-bold">{OnlineUtils.formatPrice(this.props.showNet ? this.props.product.priceNet : this.props.product.priceGross)} zł</p>
            {this.props.productVariant.isInstalmentAvailable && !this.props.marketIsB2B &&
            <a href={this.props.instalmentUrl} className={"u-padding-top-s" +
            " u-padding-s u-block u-font-bold"}>lub sprawdź ofertę raty 0%</a>}
        </div>)

    }


}


class DeviceInstalmentPayments extends React.Component {

    render() {

        let instalmentPayments = this.props.instalmentPayments;
        let oneTimePayment = this.props.oneTimePayment;

        return (
            <span>
            <p className="g-brand1-c u-font-bold">
               {
                   instalmentPayments.map(
                       instalmentPeriod =>
                           <DeviceInstalmentPeriod key={"from_" + instalmentPeriod.monthFrom}
                                                   showNet={this.props.showNet} {...instalmentPeriod}/>
                   )
               }
                <ProductOfferInstalmentPriceTooltipComponent
                    instalmentTooltipDescription={this.props.instalmentTooltipDescription}

                    proposition={this.props.proposition}
                />

            </p>
            <p className="g-gray7-c u-font-small">{oneTimePayment} zł na&nbsp;start </p>
        </span>
        )
    }
}

const mapStateToProps = (state) => ({

        deviceToChange: getChangedDevice(state),
        showNet: getPriceIsNet(state)

});


export default connect(mapStateToProps, {})(DevicePayments)
